import { Component, OnInit } from '@angular/core';
import {FormControl, Validators, NgForm} from '@angular/forms';
import { Party } from '../static/models/employee';
import {  DataService } from '../static/services/data.service';
import { ActivationEnd, Router } from '@angular/router';

@Component({
  selector: 'anms-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  constructor(public data: Party,
              public dataService: DataService,
              private router: Router,) { 
    
  }


  category = [
    {value: '1', name: 'Festival'},

  ];


  formControl = new FormControl('', [
    Validators.required,
    Validators.email
    ]);
    
    getErrorMessage() {
    return this.formControl.hasError('required') ? 'Campo Requerido' :
    '';
    }
    
    getErrorMessage_email() {
    return this.formControl.hasError('email') ? 'Ingrese un Email Valido' :
    '';
    }

  ngOnInit() {
  }


  confirmAdd(form: NgForm, content) {
    if (form.value.id == null) {
    this.dataService.postObject(form.value)
    .subscribe(data => {

    console.log(data)
    //this.resetForm(form);
    this.dataService.getObjectList();
    //this.toastr.success('Nuevo registro agregado con éxito!',' Registro de empleados');
    this.router.navigate(['/fiestas'])
    }) 
    }

    }

}



