export class Employee {
    id: number;
    name: string;
    description: string;
    createdAt: string;
    user: string;
    status: string;
}


export class User {
  id: number;
  username: string;
  email: string;
  first_name: string;
  last_name: string;
  password: string;

}

export class Party {
  id: number;
  name: string;
  description: string;
  place: string;
  address: string;
  location: string;
  event_date: string;
  category: string

}
  


