import { NgModule } from '@angular/core';

import { SharedModule } from '@app/shared';

import { StaticRoutingModule } from './static-routing.module';
import { AboutComponent } from './about/about.component';
import { FeaturesComponent } from './features/features.component';
import { Party } from '../static/models/employee';
import { FilterPipe } from '../static/services/filter.pipe';

import {MatDatepickerModule} from '@angular/material/datepicker';

import {
  MatButtonModule, MatDialogModule, MatIconModule, MatNativeDateModule, MatInputModule, MatPaginatorModule, MatSortModule, MatCardModule, MatMenuModule,
  MatTableModule, MatToolbarModule, 
} from '@angular/material';

import {MatSelectModule} from '@angular/material/select';

import { Http, HttpModule, Response, Headers, RequestOptions, RequestMethod } from '@angular/http';
import { DataService } from '../static/services/data.service';

import { ObjListComponent } from '@app/obj-list/obj-list.component';



@NgModule({
  imports: [SharedModule, StaticRoutingModule, MatDialogModule, MatInputModule, MatButtonModule, MatSelectModule, MatDatepickerModule, HttpModule, MatNativeDateModule],
  declarations: [AboutComponent, FeaturesComponent,],
  providers: [ DataService , HttpModule, Party]
})
export class StaticModule {}



