import { Title } from '@angular/platform-browser';
import { OverlayContainer } from '@angular/cdk/overlay';
import { Component, HostBinding, OnDestroy, OnInit } from '@angular/core';
import { ActivationEnd, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Subject } from 'rxjs/Subject';
import { takeUntil } from 'rxjs/operators/takeUntil';
import { filter } from 'rxjs/operators/filter';
import { DataService } from '../app/static/services/data.service';

import { Observable } from 'rxjs/Observable';
import { MatDialog } from '@angular/material';
import { ObjListComponent } from '../app/obj-list/obj-list.component';

import * as firebase from 'firebase/app';

import {
  ActionAuthLogin,
  ActionAuthLogout,
  selectorAuth,
  routerTransition
} from '@app/core';
import { environment as env } from '@env/environment';

import { NIGHT_MODE_THEME, selectorSettings } from './settings';
import { User } from '@app/static/models/employee';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'anms-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [routerTransition]
})
export class AppComponent implements OnInit, OnDestroy {




  isLoged: boolean = false;


 

  @HostBinding('class') componentCssClass;

  isProd = env.production;
  envName = env.envName;
  version = env.versions.app;
  year = new Date().getFullYear();


  constructor(
    public overlayContainer: OverlayContainer,
    private store: Store<any>,
    private router: Router,
    private titleService: Title,
    private dataService: DataService,
    public dialog: MatDialog,
    public obj: ObjListComponent
    
  ) {
    
  }


  ngOnChanges() {
    this.dataService.getObjectList()
    this.isLoged = true;
 
  }

  ngOnInit(): void {

    this.dataService.getObjectList();
    console.log(this.dataService.objectList)

    this.store
      .select(selectorSettings)
      
      
      .subscribe(settings => {
        const { theme, autoNightMode } = settings;
        const hours = new Date().getHours();
        const effectiveTheme = (autoNightMode && (hours >= 20 || hours <= 6)
          ? NIGHT_MODE_THEME
          : theme
        ).toLowerCase();
        this.componentCssClass = effectiveTheme;
        const classList = this.overlayContainer.getContainerElement().classList;
        const toRemove = Array.from(classList).filter((item: string) =>
          item.includes('-theme')
        );
        classList.remove(...toRemove);
        classList.add(effectiveTheme);
      });


  }

  ngOnDestroy(): void {
  }

  onLoginClick() {
  }






}



