import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';

import { SharedModule } from '@app/shared';
import { CoreModule } from '@app/core';

import { SettingsModule } from './settings';
import { StaticModule } from './static';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';



import { FilterPipe } from '../app/static/services/filter.pipe';
import { ObjListComponent } from '../app/obj-list/obj-list.component';
import {MatDatepickerModule} from '@angular/material/datepicker';


import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule, AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AddComponent } from './add/add.component';



@NgModule({
  imports: [
    // angular
    BrowserAnimationsModule,
    BrowserModule,

    // core & shared
    CoreModule,
    SharedModule,
    


    AngularFireDatabaseModule,
    AngularFireAuthModule,

    // features
    StaticModule,
    SettingsModule,
   

    MatDatepickerModule,


    // app
    AppRoutingModule
  ],
  
  declarations: [AppComponent, ObjListComponent, FilterPipe, AddComponent],
  providers: [AddComponent,ObjListComponent],
  bootstrap: [AppComponent],

  entryComponents: [
    AddComponent,
    ObjListComponent

  ],
})
export class AppModule {}



