import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

import {HttpClient, HttpErrorResponse} from '@angular/common/http';

import { Http, Response, Headers, RequestOptions, RequestMethod } from '@angular/http';

import { ANIMATE_ON_ROUTE_ENTER } from '@app/core';
import { Employee, User , Party} from '../../static/models/employee';
import {Router} from '@angular/router';
import { AppComponent } from '../../app.component';
import 'rxjs/add/operator/map';



@Injectable()
export class DataService {
  private readonly API_URL = 
  'https://djangular-rest.herokuapp.com/api/tickets/';

  dataChangeUser: BehaviorSubject<User[]> = new BehaviorSubject<User[]>([]);
  dataChange: BehaviorSubject<Employee[]> = new BehaviorSubject<Employee[]>([]);
  // Temporarily stores data from dialogs
  dialogData: any;


  user_get: User[];
  selectedEmployee : Employee;
  objectList : Party[];
  


  constructor (private http: Http, public router: Router) {}

  get data_users(): User[] {
    return this.dataChangeUser.value;
  }

  get data(): Employee[] {
    return this.dataChange.value;
  }

  getDialogData() {
    return this.dialogData;
  }

  getUser(){
    this.http.get('http://127.0.0.1:8000/api/usuarios/user/')
    
    .map((data : Response) =>{
      return data.json()
    }).toPromise().then(x => {
      this.user_get = x;

      this.get_token = x.auth_token

      console.log(this.user_get)
      console.log(this.get_token)

    })
  }



  /** CRUD METHODS */
  getObjectList(){
    this.http.get('https://onfire-djng.herokuapp.com/api/partys/')
    
    .map((data : Response) =>{
      return data.json() as Party[];
    }).toPromise().then(x => {
      this.objectList = x;
      console.log(this.objectList);

    })
  }

  
 
  get_token = "";
  

  ObjectPost(emp: Party){
    let headers = new Headers();
    headers.append('Authorization:', 'Token '+ this.get_token );
    let opts = new RequestOptions();
    opts.headers = headers;

    return this.http.post('https://onfire-djng.herokuapp.com/api/partys/create/', opts ).map(x => x.json());


  }

    /** CRUD METHODS */
    PostObject(emp: Party){

      var body = JSON.stringify(emp);

      let headers = new Headers({'Content-Type':'application/json'});
      headers.append('Authorization:', 'Token '+ this.get_token );
      let opts = new RequestOptions({method : RequestMethod.Post,headers : headers});
      opts.headers = headers;
  
      return this.http.post('https://onfire-djng.herokuapp.com/api/partys/create/', opts  ).map(x => x.json());
  
  
    }


    // ADD, POST METHOD
    postObject(emp : Party){
      var body = JSON.stringify(emp);
      var headerOptions = new Headers({'Content-Type':'application/json'});
      var requestOptions = new RequestOptions({method : RequestMethod.Post,headers : headerOptions});
      
      return this.http.post('https://onfire-djng.herokuapp.com/api/partys/create/', body,requestOptions).map(x => x.json());
    }
  
      // UPDATE, PUT METHOD
      putEmployee(id, emp) {
        var body = JSON.stringify(emp);
        var headerOptions = new Headers({ 'Content-Type': 'application/json' });
        var requestOptions = new RequestOptions({ method: RequestMethod.Put, headers: headerOptions });
        return this.http.put('https://djangular-rest.herokuapp.com/api/tickets/' + id + '/edit/',
          body,
          requestOptions).map(res => res.json());
      }
  
    // DELETE METHOD
    deleteEmployee(id: number) {
          return this.http.delete('https://djangular-rest.herokuapp.com/api/tickets/' + id + '/delete/').map(res => res.json());
        }
}







